/* FilterSidebar.css */
.filter-sidebar {
  width: 100%; /* –ó–∞–Ω–∏–º–∞–µ—Ç –≤—Å—é –¥–æ—Å—Ç—É–ø–Ω—É—é —à–∏—Ä–∏–Ω—É */
  padding: 10px;
  box-sizing: border-box;
}
  
.filter-sidebar h2 { /* –°–∫—Ä—ã–≤–∞–µ–º –∑–∞–≥–æ–ª–æ–≤–æ–∫ "–§–∏–ª—å—Ç—Ä—ã" */
  display: none;
}

.filter-sidebar form {
  display: flex;          /* –†–∞—Å–ø–æ–ª–∞–≥–∞–µ–º —Ñ–∏–ª—å—Ç—Ä—ã –≤ —Å—Ç—Ä–æ–∫—É */
  flex-wrap: wrap;       /* –ü–µ—Ä–µ–Ω–æ—Å–∏–º —Ñ–∏–ª—å—Ç—Ä—ã –Ω–∞ –Ω–æ–≤—É—é —Å—Ç—Ä–æ–∫—É, –µ—Å–ª–∏ –Ω–µ –ø–æ–º–µ—â–∞—é—Ç—Å—è */
  justify-content: center;
  align-items: center; /* Center items vertically */
}

.filter-sidebar form > div {
  margin: 5px;            /* –û—Ç—Å—Ç—É–ø—ã –º–µ–∂–¥—É —Ñ–∏–ª—å—Ç—Ä–∞–º–∏ */
}

.filter-sidebar label {
  display: block;        /* –†–∞–∑–º–µ—â–∞–µ–º –ª–µ–π–±–ª –Ω–∞–¥ —Å–µ–ª–µ–∫—Ç–æ–º */
  margin-bottom: 3px;
  font-size: 0.8em;
  text-align: center;   /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º —Ç–µ–∫—Å—Ç –ª–µ–π–±–ª–∞ */
}

.filter-sidebar select {
  padding: 5px;           /* –í–Ω—É—Ç—Ä–µ–Ω–Ω–∏–µ –æ—Ç—Å—Ç—É–ø—ã –≤ —Å–µ–ª–µ–∫—Ç–µ */
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 0.9em;
  width: 120px;           /* Fixed width for each select */
}

.filter-sidebar button {
  display: none;         /* –°–∫—Ä—ã–≤–∞–µ–º –∫–Ω–æ–ø–∫—É "–ü—Ä–∏–º–µ–Ω–∏—Ç—å" */
}

/* –ü–û–õ–ó–£–£–£–£–£–£–£–£–£–£–£–£–£–£–£–£–£–ù–û–û–û–û–û–û–û–û–û–û–ö –¶–ï–ù–´*/
.price-range-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-bottom: 1rem;
}

.price-label {
  font-size: 0.9rem;
  margin-bottom: 0.5rem;
}

.price-input {
  width: 100%;
  -webkit-appearance: none; /* –°–±—Ä–æ—Å —Å—Ç–∏–ª–µ–π –±—Ä–∞—É–∑–µ—Ä–∞ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é */
  height: 8px;
  border-radius: 4px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

.price-input:hover {
  opacity: 1;
}

.price-input::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: rgb(0, 104, 74);
  cursor: pointer;
}

.price-input::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #4CAF50;
  cursor: pointer;
}

.price-value {
  font-size: 0.8rem;
  margin-top: 0.5rem;
  color: #555;
}


@media (max-width: 600px) {
  .filter-sidebar form {
    flex-direction: column;
    align-items: stretch;
  }

  .filter-sidebar form > div {
    width: 100%;
    margin: 5px 0;
  }

  .filter-sidebar label {
    text-align: left;
  }

  .filter-sidebar select {
    width: 100%;
  }
}