/* ProductList.css */

.product-list {
    display: grid;
    /* –ò–∑–º–µ–Ω–µ–Ω–æ: —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–æ–µ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å—Ç–æ–ª–±—Ü–æ–≤ */
    grid-template-columns: repeat(4, 1fr); /* 4 –∫–æ–ª–æ–Ω–∫–∏ –æ–¥–∏–Ω–∞–∫–æ–≤–æ–π —à–∏—Ä–∏–Ω—ã */
    gap: 30px;
    list-style: none;
    padding: 0;
    /* –ò–∑–º–µ–Ω–µ–Ω–æ: —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è –º–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
    max-width: 1220px; /* –®–∏—Ä–∏–Ω–∞ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ = 4 –∫–æ–ª–æ–Ω–∫–∏ * 250px + 3 –∑–∞–∑–æ—Ä–∞ * 20px */
    margin: 0 auto; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
  }
  
  .product-item {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: center;
    /* –î–æ–±–∞–≤–ª–µ–Ω–æ: –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è –≤—ã—Å–æ—Ç–∞ */
    height: 350px; /* –ü—Ä–∏–º–µ—Ä —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–æ–π –≤—ã—Å–æ—Ç—ã */
    box-sizing: border-box; /* –í–∞–∂–Ω–æ –¥–ª—è —É—á–µ—Ç–∞ padding –∏ border */
  }
  
  .product-item img {
    max-width: 100%;
    height: auto;
    margin-bottom: 10px;
  }
  
  .product-item h3 {
    font-size: 1.2em;
    margin-bottom: 5px;
  }
  
  .product-item p {
    font-size: 1em;
    margin-bottom: 5px;
  }
  